@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");


// search-input
.search-result {
  width: 1170px;
  height: 100%;
  max-height: 80vh;
  border-radius: 8px;
  background-color: #fff;
  overflow: auto;
  scrollbar-width: none;

  .search-title {
    font-size: 20px;
    padding-bottom: 20px;
    margin: 0;
    font-family: "Poppins", sans-serif;
  }
}

// modal
.modal-container {
  display: flex;
  width: 80%;
  height: 80%;
  background-color: #caf1f1;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);

  // logo
  .logo-merchant {
    width: 48%;
    height: 100%;
    background-color: #e3e5fa;
    position: relative;
  }

  .icon-back {
    position: absolute;
    color: #6f768d;
    padding: 36px 36px;
    width: 40px;
    height: 40px;
  }

  .ellpse {
    position: absolute;
    width: 80%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 20%);
  }

  .img-merchant {
    width: 70%;
    height: 80%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

//content modal
.content-modal {
    flex: 1;
  padding: 0;
  margin: 20px 20px;
  scrollbar-width: none;
  overflow: auto;
}

.container-content {
  display: flex;
  align-items: center;
  margin: 0;
}

.name-merchant {
  color: #000;
  text-align: left;
  font-size: 25px;
  font-weight: 600;
  font-family: "Montserrat Alternates", "sans-serif";
  border: none;
}

.line {
  height: 30px;
  width: 1px;
  background-color: #bfbfbf;
  margin-left: 10px;
}

.type-merchant {
  color: #50cc98;
  font-family: "Montserrat Alternates", "sans-serif";
  font-size: 15px;
  font-weight: 400;
  margin-left: 15px;
  border: none;
}

.wrapper-content {
  display: flex;
  width: 90%;
  height: 8.1%;
  align-items: center;
  margin-top: 30px;
  padding: 0;
}

.title-merchant {
  align-items: center;
  padding: 0;
  width: 32%;
  height: 100%;
  color: #000;
  font-size: 20px;
  font-weight: 600;
  font-family: "Montserrat Alternates", "sans-serif";
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: 10px;
  display: flex;
  align-items: center;
}

.content-merchant {
  flex: 1;
